<template>
  <div class="MyInfo">

    <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
    </van-row>
    <h1>{{ msg }}</h1>
    <h2>你可以在这里修改页面设置</h2>



    <div>
        <p></p>
        <p><van-button type="primary" to="/">退出登入</van-button></p>
        <p></p>
        <p></p>
        <p></p>
    </div>

    <div>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
    </div>
    

    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>
  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'Setting',
  data () {
    return {
      msg: '页面设置',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



