<template>
    <div id='csorders'>
        <!-- 页首 -->
        <navig-bar></navig-bar>
        <!-- 页面主体 -->
        <div>
            <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
                <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text="没有更多了"
                        @load="onLoad"
                >
                    <van-cell v-for="item in list" :key="item" :title="item.title" :value="item.value" :label="item.label" @click="onclick(item.title)">
                        <template #icon>
                            <van-image
                                    round
                                    width="45"
                                    height="45"
                                    src="http://imagemxc.test.upcdn.net//订单 (1).png"
                            >
                            </van-image>
                        </template>
                        <template #label>
                            <div v-if="item.viewed==1" style="color: #40FF40">已审核</div>
                            <div v-if="item.viewed==0" style="color: #A9A9A9">未审核</div>
                        </template>
                    </van-cell>
                </van-list>
            </van-pull-refresh>
        </div>

        <!-- 选项菜单 -->
        <van-action-sheet v-model="show" :actions="actions" :color="actions.color"  @select="onSelect" />
        <!--删除-->
        <van-popup v-model="showDeletePanel" style="width: 200px; height: 150px" @close="cancelDelete" round>
            <div style="font-size: 20px; color: #FF0000 ; margin-top: 30px">您确定要删除该订单吗？</div>
            <van-button type="danger" @click="submitDelete">确认</van-button>
            <van-button type="default" @click="cancelDelete">取消</van-button>
        </van-popup>
        <!-- 底边栏 -->
        <div>
            <tabBar></tabBar>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import navigBar from '../navigBar';
    import tabBar from "../tabBar";

    // import { NavBar } from 'vant';
    // Vue.use(NavBar);
    new Vue({
    });
    export default {
        name: 'CSOrders',
        el: '#csorder',
        components: {
            tabBar,
            navigBar
        },
        data() {
            return {
                show: false,
                actions: [
                    { name: '查看详情' },
                    { name: '删除合同' ,color: "#ff0000"},
                ],
                testlist: [
                    {title: "O14412",value: "新北区15-429", viewed: "1"},
                    {title: "O17715",value: "新北区15-431", viewed: "0"},
                    {title: "O23046",value: "新北区15-433", viewed: "0"},
                    {title: "O17788",value: "新北区15-429", viewed: "1"},
                    {title: "O73641",value: "新北区15-431", viewed: "0"},
                    {title: "O12560",value: "新北区15-433", viewed: "0"}
                ],
                list: [

                ],
                loading: false,
                finished: false,
                refreshing: false,
                thisOrder: '',
                showDeletePanel: false,
            }
        },
        methods: {
            onLoad() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < this.testlist.length; i++) {
                        this.list.push(this.testlist[i]);
                    }

                    // 加载状态结束
                    this.loading = false;
                    this.finished = true;

                    // 数据全部加载完成
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onSelect(item) {
                // 默认情况下点击选项时不会自动收起
                // 可以通过 close-on-click-action 属性开启自动收起
                this.show = false;
                // Toast(item.name);
                if(item.name=='查看详情')
                    this.$router.push("/CS/CSOrders/"+this.thisOrder);
                else{
                    this.show=false;
                    this.showDeletePanel = true;
                }

            },
            onclick(orderId){
                this.show = true;
                this.thisOrder = orderId;
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 清空列表数据
                this.finished = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loading = true;
                this.onLoad();
            },
            cancelDelete(){
                this.showDeletePanel =false;
            },
            submitDelete(){
                this.showDeletePanel = false;
                Toast.success("删除成功");
                this.refreshing=true;
                this.onRefresh();
            }
        }
    }
    import { Toast } from 'vant';
    Vue.use(Toast);
</script>

<style scoped>

</style>


