<template>
    <div>
    <!--  页首  -->
        <navig-bar></navig-bar>
    <!--  页面  -->
        <div>
            <van-tabs>
                <van-tab title="报修">
                    <!--搜索栏-->
                    <van-search v-model="searchKey" clearable @search="onSearch" @clear="onClear" placeholder="请输入搜索关键词" />
                    <!--工单列表-->
                    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                finished-text="没有更多了"
                                @load="onLoad"
                        >
                            <van-cell v-for="item in list" :key="item" :title="item.WorkOrderId" :value="item.RoomId"  @click="onclickWO(item.WorkOrderId)">
                                <template #icon>
                                    <van-image
                                            round
                                            width="45"
                                            height="45"
                                            src="http://imagemxc.test.upcdn.net//工单.png"
                                    >
                                    </van-image>
                                </template>
                                <template #label>
                                    <div v-if="item.ServiceId==false" style="color:#EE0000">待建立工单</div>
                                    <div v-else style="color: #40FF40">已建立工单</div>
                                </template>
                            </van-cell>
                        </van-list>
                    </van-pull-refresh>
                </van-tab>
                <van-tab title="投诉">
                    <!--搜索栏-->
                    <van-search v-model="searchKey" clearable @search="onSearchComplaints" @clear="onClearComplaints" placeholder="请输入搜索关键词" />
                    <!--投诉列表-->
                    <van-pull-refresh v-model="refreshingComplaints" @refresh="onRefreshComplaints">
                        <van-list
                                v-model="loadingComplaints"
                                :finished="finishedComplaints"
                                finished-text="没有更多了"
                                @load="onLoadComplaints"
                        >
                            <van-cell v-for="item in listComplaints" :key="item" :title="item.ComplaintID" :value="item.ComplaintUser" @click="showReply(item.ComplaintItem)">
                                <template #icon>
                                    <van-image
                                            round
                                            width="45"
                                            height="45"
                                            src="http://imagemxc.test.upcdn.net//投诉.png"
                                    >
                                    </van-image>
                                </template>
                                <template #label>
                                    <div v-if="item.replied==false" style="color: #EE0000">待回复</div>
                                    <div v-else style="color: #40FF40">已回复</div>
                                </template>
                            </van-cell>
                        </van-list>
                    </van-pull-refresh>
                </van-tab>
            </van-tabs>
            <!--  投诉托盘  -->
            <van-popup v-model="showReplyPanel" position="bottom" @close="cancelReply" :style="{ height: '60%' }">
                <van-form @submit="submitReply">
                    <h4>回复投诉</h4>
                    <van-cell title="投诉内容:" value=""></van-cell>
                    <div style="color: #A9A9A9; float:left; margin-left: 32px; font-size: 14px" v-html="thisItem"></div>
                    <van-field
                            v-model="editReply"
                            name="回复内容"
                            label="回复内容"
                            placeholder="请填写回复内容"
                            :rules="[{ required: true, message: '请填写回复内容' }]"
                    />
                    <van-button round block type="info" native-type="submit">
                        提交
                    </van-button>
                </van-form>
            </van-popup>
        </div>
    <!--  页尾  -->
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import tabBar from "../tabBar";
    import navigBar from "../navigBar";
    import Vue from 'vue';
    import {Toast} from 'vant';
    Vue.use(Toast);
    export default {
        name: 'CSWorkOrder',
        components: {
            tabBar,
            navigBar
        },
        data(){
            return{
                workOrders: [
                    { WorkOrderId: 'WO1322', RoomId: 'R1312', RepairmanId: "", ServiceId: "", WorkOrderItem: "维修灯", replied: false},
                    { WorkOrderId: 'WO1322', RoomId: 'R1312', RepairmanId: "RP13112412", ServiceId: "S2222", WorkOrderItem: "换水龙头"},
                    { WorkOrderId: 'WO1232', RoomId: 'R1322', RepairmanId: "RP13112412", ServiceId: "S3332", WorkOrderItem: "换门把"},
                ],
                list: [
                    
                ],
                complaints: [
                    { ComplaintID: 'CP1444', ComplaintUser: 'U13442513', ComplaintItem: '房东拒绝退还押金', replied: false},
                    { ComplaintID: 'CP1555', ComplaintUser: 'U23142151', ComplaintItem: 'S17384客服态度差', replied: true},
                ],
                listComplaints: [

                ],
                searchKey: '',
                loading: false,
                finished: false,
                refreshing: false,
                loadingComplaints: false,
                finishedComplaints: false,
                refreshingComplaints: false,
                showReplyPanel: false,
                thisItem: '',
                editReply: '',
            }
        },
        methods: {
            onSearch(){
                this.list=[];
                for(let i=0;i<this.workOrders.length; i++){
                    if(this.workOrders[i].WorkOrderId.search(this.searchKey)!=-1)
                        this.list.push(this.workOrders[i])
                }
            },
            onClear(){
                this.searchKey='';
                this.refreshing=true;
                this.onRefresh();
            },
            onLoad() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < this.workOrders.length; i++) {
                        this.list.push(this.workOrders[i]);
                    }

                    // 加载状态结束
                    this.loading = false;
                    this.finished = true;

                    // 数据全部加载完成
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 清空列表数据
                this.finished = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loading = true;
                this.onLoad();
            },
            onclickWO(woID){
                this.$router.push({path: '/CS/CSWorkOrders/'+woID});
            },
            onSearchComplaints(){
                this.listComplaints=[];
                for(let i=0;i<this.complaints.length; i++){
                    if(this.complaints[i].ComplaintID.search(this.searchKey)!=-1)
                        this.listComplaints.push(this.complaints[i])
                }
            },
            onClearComplaints(){
                this.searchKey='';
                this.refreshingComplaints=true;
                this.onRefreshComplaints();
            },
            onLoadComplaints() {
                // 异步更新数据
                // setTimeout 仅做示例，真实场景中一般为 ajax 请求
                setTimeout(() => {
                    for (let i = 0; i < this.complaints.length; i++) {
                        this.listComplaints.push(this.complaints[i]);
                    }

                    // 加载状态结束
                    this.loadingComplaints = false;
                    this.finishedComplaints = true;

                    // 数据全部加载完成
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onRefreshComplaints() {
                if (this.refreshingComplaints) {
                    this.listComplaints = [];
                    this.refreshingComplaints = false;
                }

                // 清空列表数据
                this.finishedComplaints = false;

                // 重新加载数据
                // 将 loading 设置为 true，表示处于加载状态
                this.loadingComplaints = true;
                this.onLoadComplaints();
            },
            showReply(item){
                this.thisItem=item;
                this.showReplyPanel = true;
            },
            cancelReply(){
                this.showReplyPanel = false;
            },
            submitReply(){
                this.showReplyPanel = false;
                this.refreshingComplaints=true;
                this.onRefreshComplaints();
                Toast.success("回复成功");
            }
        }
    }
</script>

<style scoped>

</style>


