<template>
  <div class="picture">
  <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
  </div>
    <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
  </van-row>

    <p><h1>{{ msg }}</h1></p>



    <div>
    <van-form @submit="onSubmit">

      <van-cell-group>
        <van-field
        v-model="message"
        rows="5"
        autosize
        label="留言"
        type="textarea"
        maxlength="150"
        placeholder="请输入您想要反应的问题并上传照片，我们的客服会在第一时间给出答复"
        show-word-limit/>
      </van-cell-group> 

      <div style="margin:16px">
        <van-uploader :after-read="afterRead" v-model="fileList" multiple :max-count="3" />
      </div>
        <div style="margin-top: 16px;">
        <van-button round  type="warning" native-type="submit" >提交</van-button>
        </div>
    </van-form>
  </div>

    <!--<van-cell v-for="(item,index) in fileList" :key="item">
            <span style="font-size:1px;">{{fileList[index]}}</span>
        </van-cell>-->
    

    <div>
    <p>可以通过以下方式直接联系客服</p>
    客服电话:{{phone}} 客服邮箱:{{email}} 客服微信:{{weixin}}
    </div>


    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>

  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'Renter',
  data () {
    return {
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      msg: '有问题，找客服',
      imgSrc:require('../assets/ConnectService.png'),
      fileList: [],
      value:'',
      message:'',
      phone:'17815566221',
      email:'1322496098@qq.com',
      weixin:'17815566221',
    };
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      afterRead(file) {
          console.log(file)
          let formData = new FormData()
          formData.append('avatar', file.file)
          console.log(formData)
          this.$axios.post('../assets', formData, {
              headers: {
                  'enctype': 'multipart/form-data'
              }
          })
          .then(res => {
              console.log(res.data)
          })
      },
      onSubmit(values) {
          console.log('submit', values);
          this.$router.push({
              path:'/ConnectService',
          });
      },
  },
  created(){
      this.id=this.$route.query.name;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1{
  font-weight: normal;
}
.background{
    width:100%;  
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: absolute;
}
 
.front{
    z-index:1;
    position: absolute;
}
</style>



