<template>
  <div class="Paying">
    <h1>{{ msg }}</h1>
    <h2>你需要缴纳{{money}}元人民币</h2>
    {{tip}}
    <div>
        <p><van-button type="primary" @click="paying" :disabled="disabled">确认缴费</van-button></p>
        <p><van-button type="primary" to="/CheckRoom">返回</van-button></p>
        <p></p>
        <p></p>
        <p></p>
    </div>

    <div>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
    </div>
    

        
  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'Paying',
  data () {
    return {
      msg: '正在缴费',
      tip:'还未缴费',
      disabled:false,
    }
  },
  methods:{
      paying(){
          this.money='0';
          this.tip='缴费成功！';
          this.disabled=true;
      }
  },
  components:{
      Foot_bar
  },
  created(){
      this.money = this.$route.query.money;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



