<template>
    <div>
        <!--页首-->
        <navig-bar></navig-bar>

        <div>
            <!-- 个人页面展示 -->
            <!-- 头像 -->
            <div style="margin-top:30%; margin-bottom: 10%">
                <van-image
                        round
                        width="120"
                        height="120"
                        v-bind:src=userAvatar
                ></van-image>
            </div>
            <!--ID-->
            <div id="showID">{{userNickname}}<van-icon name="edit" style="line-height: inherit;" @click="setNickname"/></div>
            <van-cell-group>
                <van-cell title="租客账号:" :value="userId" icon="records"/>
                <van-cell title="真实姓名:" :value="userName" icon="user-o" @click="setName"/>
                <van-cell title="真实性别:" :value="userSex" icon="friends-o" @click="setSex"/>
                <van-cell title="手机号码:" :value="userPhoneNumber" icon="phone-o" @click="setPhoneNumber"/>
            </van-cell-group>
        </div>
        <!--修改姓名托盘-->
        <van-popup v-model="showNameEdit" position="bottom" @close="cancelName" :style="{ height: '30%' }">
            <h5>修改姓名</h5>
            <van-form @submit="submitName">
                <van-field
                        v-model="newName"
                        name="真实姓名"
                        label="真实姓名"
                        placeholder="请填写该用户的真实姓名"
                        :rules="[{ required: true, message: '请填写该用户的真实姓名' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        提交
                    </van-button>
                </div>

            </van-form>
        </van-popup>
        <!--修改性别托盘-->
        <van-popup v-model="showSexEdit" position="bottom" @close="cancelSex" :style="{ height: '30%' }">
            <van-picker
                    show-toolbar
                    visible-item-count="3"
                    :columns="columns"
                    @confirm="submitSex"
                    @cancel="cancelSex"
                    @close="cancelSex"
            >
                <template #title>
                    <h5>修改性别</h5>
                </template>
            </van-picker>
        </van-popup>
        <!--修改手机号码托盘-->
        <van-popup v-model="showPhoneEdit" position="bottom" @close="cancelPhoneNumber" :style="{ height: '30%' }">
            <h5>修改手机号码</h5>
            <van-form @submit="submitPhoneNumber">
                <van-field
                        v-model="newPhoneNumber"
                        name="手机号码"
                        label="手机号码"
                        placeholder="请填写该用户的手机号码"
                        :rules="[{ required: true, message: '请填写该用户的手机号码' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        提交
                    </van-button>
                </div>

            </van-form>
        </van-popup>
        <!--修改昵称-->
        <van-popup v-model="showNickEdit" position="bottom" @close="cancelNickname" :style="{ height: '30%' }">
            <h5>修改昵称</h5>
            <van-form @submit="submitNickname">
                <van-field
                        v-model="newNickname"
                        name="昵称"
                        label="昵称"
                        placeholder="请填写该用户的昵称"
                        :rules="[{ required: true, message: '请填写该用户的昵称' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        提交
                    </van-button>
                </div>
            </van-form>
        </van-popup>
        <!--页尾-->
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import Vue from 'vue';
    import navigBar from '../navigBar';
    import tabBar from "../tabBar";
    export default {
        name: 'userHomeSP',
        components: {
            navigBar,
            tabBar,
        },
        data() {
            return{
                userId: '',
                userNickname: '张飞喝断当阳桥',
                userName: '张飞',
                userSex: '男',
                userPhoneNumber: '12345678910',
                userAvatar: 'http://imagemxc.test.upcdn.net//用户.png',
                showNickEdit: false,
                showNameEdit: false,
                showSexEdit: false,
                showPhoneEdit: false,
                showAvatarEdit: false,
                newNickname: '',
                newName: '',
                newSex: '',
                newPhoneNumber: '',
                columns: [
                    '男', '女'
                ],
                activeNames: ['1'],
            }
        },
        created() {
            this.userId = this.$route.params.id;
        },
        methods: {
            setName(){
                this.showNameEdit = true;
            },
            submitName(){
                this.showNameEdit = false;
                Toast.success("修改成功");
                this.userName = this.newName;
            },
            cancelName(){
                this.showNameEdit = false;
            },
            setSex(){
                this.showSexEdit = true;
            },
            submitSex(value){
                this.showSexEdit = false;
                Toast.success("修改成功");
                this.userSex = value;
            },
            cancelSex(){
                this.showSexEdit = false;
            },
            setPhoneNumber(){
                this.showPhoneEdit = true;
            },
            submitPhoneNumber(){
                this.showPhoneEdit = false;
                Toast.success("修改成功");
                this.userPhoneNumber = this.newPhoneNumber;
            },
            cancelPhoneNumber(){
                this.showPhoneEdit = false;
            },
            setNickname(){
                this.showNickEdit = true;
            },
            submitNickname(){
                this.showNickEdit = false;
                Toast.success("修改成功");
                this.userNickname = this.newNickname;
            },
            cancelNickname(){
                this.showNickEdit = false;
            },
            afterRead(){
                Toast.success("上传成功");
            },
        }
    }
    import {Toast} from 'vant';
    Vue.use(Toast);
</script>

<style scoped>

</style>


