<template>
    <div>
        <navig-bar></navig-bar>
        <div>
            <van-swipe style="width: 100%; height:200px" :autoplay="3000">
                <van-swipe-item v-for="(image, index) in images" :key="index">
                    <img width="100%" v-lazy="image" />
                </van-swipe-item>
            </van-swipe>
            <van-cell-group>
                <van-cell title="房间编号" center :value="RoomId" icon="records"></van-cell>
                <van-cell title="房间类型" center :value="RoomType" icon="wap-home-o"></van-cell>
                <van-cell title="租金" center :value="RoomRent" icon="gold-coin-o"></van-cell>
                <van-cell title="房东姓名" center :value="OwnerName" icon="wap-home-o"></van-cell>
                <van-cell title="联系电话" center :value="OwnerPhone" icon="wap-home-o"></van-cell>
                <van-cell title="房间地址" center :value="RoomAddress" icon="location-o"></van-cell>
                <van-cell title="状态" center icon="bill-o">
                    <template #default>
                        <div v-if="RoomAvailable" style="color: #40FF40">
                            房间可用
                        </div>
                        <div v-else style="color: #FF0000">
                            房间不可用
                        </div>
                    </template>
                </van-cell>
                <van-cell title="房间备注" center value="" icon="notes-o"></van-cell>
                <div v-html="RoomItem" style="color: #A9A9A9; font-size: 12px; margin-left: 20px; margin-right: 20px; margin-top: 10px; margin-bottom: 20px; text-align: left; line-height: 150%" ></div>
            </van-cell-group>
            <!--提交按钮-->
            <van-button round style="width: 80%; margin-bottom: 10px" type="primary" @click="showBook" :disabled="!RoomAvailable">租赁房间</van-button>
            <van-button round style="width: 80%; margin-bottom: 80px" @click="showEdit">修改信息</van-button>
            <!--修改按钮-->
            <!--修改属性弹出层-->
            <div>
                <!--租赁房屋-->
                <van-popup round position="bottom" :style="{ height: '30%' }" @close="cancelBook" v-model="showBookPanel">
                    <van-form @submit="submitBook">
                        <van-field
                                v-model="bookData.userId"
                                name="租客账号"
                                label="租客账号"
                                placeholder="租客账号"
                                :rules="[{ required: true, message: '请填写租客账号' }]"
                        />
                        <van-field
                                v-model="bookData.bookType"
                                name="租约类型"
                                label="租约类型"
                                placeholder="租约类型"
                                :rules="[{ required: true, message: '请填写租约类型' }]"
                        />
                        <van-field
                                v-model="bookData.bookComment"
                                name="订单备注"
                                label="订单备注"
                                placeholder="订单备注"
                                :rules="[{ required: true, message: '请填写订单备注' }]"
                        />
                        <van-button round block type="info" native-type="submit">
                            提交
                        </van-button>
                    </van-form>
                </van-popup>
                <!--修改信息-->
                <van-popup round position="bottom" :style="{ height: '90%' }" @close="cancelEdit" v-model="showEditPanel">
                    <van-form @submit="submitEdit">
                        <h5>修改信息</h5>
                        <van-field
                                v-model="editData.RoomType"
                                name="房间类型"
                                label="房间类型"
                                placeholder="房间类型"
                                :rules="[{ required: true, message: '请填写房间类型' }]"
                        />
                        <van-field
                                v-model="editData.RoomAvailable"
                                name="房间是否可用"
                                label="房间是否可用"
                                placeholder="房间是否可用(true/false)"
                                :rules="[{ required: true, message: '请填写房间是否可用' }]"
                        />
                        <van-field
                                v-model="editData.RoomRent"
                                type="number"
                                name="租金"
                                label="租金"
                                placeholder="租金(数字)"
                                :rules="[{ required: true, message: '请填写租金(数字)' }]"
                        />
                        <van-field
                                v-model="editData.OwnerName"
                                name="房东姓名"
                                label="房东姓名"
                                placeholder="房东姓名"
                                :rules="[{ required: true, message: '请填写房东姓名' }]"
                        />
                        <van-field
                                v-model="editData.OwnerPhone"
                                name="房东联系电话"
                                type="tel"
                                label="房东联系电话"
                                placeholder="房东联系电话"
                                :rules="[{ required: true, message: '请填写房东联系电话' }]"
                        />
                        <van-field
                                v-model="editData.RoomAddress"
                                name="房间地址"
                                label="房间地址"
                                placeholder="房间地址"
                                :rules="[{ required: true, message: '请填写房间地址' }]"
                        />
                        <van-field
                                v-model="editData.RoomItem"
                                type="textarea"
                                :autosize="{maxHeight: 150, minHeight: 150}"
                                name="房间备注"
                                label="房间备注"
                                placeholder="房间备注"
                                :rules="[{ required: true, message: '请填写房间备注' }]"
                        />
                        <van-button style="margin-bottom: 10px" round block type="info" native-type="submit">
                            提交
                        </van-button>
                    </van-form>
                </van-popup>
            </div>
        </div>
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import navigBar from "../navigBar";
    import tabBar from "../tabBar";
    import Vue from 'vue';
    import { Swipe, SwipeItem } from 'vant';
    Vue.use(Swipe);
    Vue.use(SwipeItem);
    import {Lazyload} from 'vant';
    Vue.use(Lazyload);
    import {Toast} from 'vant';
    Vue.use(Toast);

    export default {
        name: 'roomSP',
        components: {
            navigBar,
            tabBar,
        },
        created() {
            this.RoomId = this.$route.params.id;
        },
        data(){
            return {
                RoomId: 'R12198',
                RoomType: '新北区15号公寓',
                RoomRent: 1000.0,
                OwnerName: '王石',
                OwnerPhone: '12345678910',
                RoomAddress: "北京市海淀区学院路37号内15号学生公寓",
                RoomItem: "1、【装修环境】：精装修，家具家电齐全，图片是摄影师去拍的，真实，室内设施可以看图片，这种配置租这个价位合理。业主诚意出租，现在空置看房：提前预约即可，您方便的时候给我一个电话，尽快带您看房。<br />" +
                    "<br />" +
                    "2、【房屋配置】：家电家具全齐全新，拎包入住，房子装修的好，干净整洁，温馨舒适。<br />" +
                    "<br />" +
                    "3、【周边配套】：万福广场电影院医院众多公交四通八达餐饮；日本料理韩国料理海鲜中国传统美食齐全应有尽有北宁公园新鲜空气周围商圈成熟。<br />" +
                    "<br />" +
                    "4、【南北通透】：正规南北通透两居，空气对流新鲜，易于身心健康！全明格局，采光好，楼层高，阳光充足<br />" +
                    "<br />" +
                    "5、【房子情况】：业主一直自住，不是为了出租而装修、配家具。平时很爱惜房子，所以房子保持特别好。<br />" +
                    "<br />" +
                    "6、【随时看房】：只要提前预约，都方便看房，业主可以过来。<br />" +
                    "<br />" +
                    "7、【价格合适】：所以本房性价比很高！<br />" +
                    "<br />" +
                    "8、【业主要求】：租户爱惜房子，保持房子干净整洁，不要随便违约！",
                RoomAvailable: true,
                images: [
                    'https://tse2-mm.cn.bing.net/th/id/OIP.JiQ43UueFdfLT2wrIaTgIwHaFD?w=300&h=203&c=7&o=5&dpr=1.25&pid=1.7',
                    'https://tse1-mm.cn.bing.net/th/id/OIP.CzSR3Ddp2MhB3DM0u4K9CwHaEq?w=300&h=188&c=7&o=5&dpr=1.25&pid=1.7',
                    'https://tse3-mm.cn.bing.net/th/id/OIP.lcHGw9YMxsiftAaFt6XbuwHaE8?w=282&h=188&c=7&o=5&dpr=1.25&pid=1.7'
                ],
                showEditPanel: false,
                showBookPanel: false,
                bookData: {
                    userId: '',
                    bookType: '',
                    bookComment: '',
                },
                editData: {
                    RoomType: '',
                    RoomAvailable: true,
                    RoomRent: '999999',
                    OwnerName: '',
                    OwnerPhone: '',
                    RoomItem: '',
                    RoomAddress: ''
                }
            }
        },
        methods: {
            showEdit(){
                this.editData.OwnerName=this.OwnerName;
                this.editData.OwnerPhone=this.OwnerPhone;
                this.editData.RoomAvailable=this.RoomAvailable;
                this.editData.RoomItem=this.RoomItem;
                this.editData.RoomRent=this.RoomRent;
                this.editData.RoomType=this.RoomType;
                this.editData.RoomAddress=this.RoomAddress;
                this.showEditPanel = true;
            },
            cancelEdit(){
                this.showEditPanel = false;
            },
            submitEdit(){
                this.OwnerName=this.editData.OwnerName;
                this.OwnerPhone=this.editData.OwnerPhone;
                this.RoomAvailable=this.editData.RoomAvailable;
                this.RoomItem=this.editData.RoomItem;
                this.RoomRent=this.editData.RoomRent;
                this.RoomType=this.editData.RoomType;
                this.RoomAddress=this.editData.RoomAddress;
                this.showEditPanel = false;
                Toast.success("修改成功");
            },
            showBook(){
                this.showBookPanel = true;
            },
            cancelBook(){
                this.showBookPanel = false;
            },
            submitBook(){
                Toast.success("租赁成功");
                this.RoomAvailable = false;
                this.showBookPanel = false;
            }
        }
    }
</script>

<style scoped>
</style>


