<template>
  <div class="CheckRoom">

      <van-sticky >

    <van-nav-bar
    title="顷刻"
    left-text="返回"
    :right-text="myid"
    left-arrow
    @click-left="onClickLeft"
    @click-right="onClickRight"/>

  </van-sticky>

    <h1>{{ msg1 }}</h1>
    <h2 style="color:red;">{{days}}天</h2>

        <p><van-cell  size="large" title="查询本月需交费用" is-link arrow-direction="down" @click="check"/></p>
    <div v-if="v1">
        <van-cell v-for="(item,index) in paylist" :key="item" clickable @click="clicktopay(item)">
            <span style="font-size:25px;">{{paylist[index]}}</span>
        </van-cell>
    </div>

    <div v-if="v2">
        没有费用需要交。
    </div>

  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'CheckRoom',
  data () {
    return {
      msg1: '距离租约到期还有：',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      days:'100',
      msg2:'本月应交',
      paylist:[
          '1',
          '2',
      ],
      v1:false,
      v2:false,
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      check(){
          if(this.paylist.length==0){
              this.v1=false;
              this.v2=true;
          }
          else{
              this.v1=true;
              this.v2=false;
          }
      },
      onClickLeft() {
          this.$router.push({
            path:'/MyInfo',
          });
      },
      onClickRight() {
          this.$router.push({
            path:'/MyInfo',
          });
    },
    clicktopay(k){
        this.$router.push({
            path:'/Paying',
            query:{money:k},
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



